@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
html {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", -apple-system, system-ui, sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid #000;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid #000;background-color:#fff;opacity:.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}
.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}
.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 80px}.directory-menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%}.menu-item{align-items:center;border:1px solid #000;display:flex;flex:1 1 auto;height:240px;justify-content:center;margin:0 7.5px 15px;min-width:30%}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .content{border:1px solid #000;display:flex;align-items:center;flex-direction:column;height:90px;justify-content:center;padding:0 25px}.menu-item .content .title{color:#4a4a4a;font-size:22px;font-weight:bold;margin-bottom:6px}.menu-item .content .subtitle{font-size:16px;font-weight:lighter}

